import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

const PROJECT_MAP = {
    mcm2019: {
        title: "Analysis of US Opioid Trends Using SIR Models, Stochastic Process, and Machine Learning",
    },
    cgscheduler: {
        title: "Cell Group Meeting Scheduler",
    },
    aocmm2018: {
        title: "ViralSim",
    },
    mcm2018: {
        title: "Interdisciplinary Analysis of the Energy Profiles of Various States",
    },
};

const styles = {
    card: {
        borderRadius: 0,
        maxWidth: '60vw',
    }
};

const ProjectEntry = ({ title }) => (
    <h1>{title}</h1>
);

class Projects extends Component {
    render(){
        const { classes } = this.props;
        return (
            <div id="projects-content">
                <Typography
                    variant="h2"
                    style={{
                        color: 'white',
                        fontWeight: 500,
                        margin: '4vw 0',
                        transition: '0.2s',
                    }}
                >Personal Projects</Typography>
                <Grid container>
                    <Grid item>
                        <Card className={classes.card}>
                            <CardContent>
                                <ProjectEntry title="Interdisciplinary Analysis of the Energy Profiles of Various States"/>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item></Grid>
                </Grid>
                    <div className="projects-row">
                        <ProjectEntry title="Cell Group Meeting Scheduler"/>
                    </div>
                    <div className="projects-row">
                        <ProjectEntry title="ViralSim"/>
                        <ProjectEntry title="Georadio"/>
                    </div>
                    <h3>More can be found at my [Github]!</h3>
            </div>
        );
    }
}

export default withStyles(styles)(Projects);